import React from "react"
import styled from "styled-components"
import Header from "../components/header.tsx"
import Footer from "../components/footer.tsx"
import SEO from "../components/seo.tsx"
import { graphql, Link } from "gatsby"
import { FaRegCalendar } from "react-icons/fa"
import { DiscussionEmbed } from "disqus-react"

const MainContainer = styled.div`

`

const SubContainer = styled.div`
	width: 100%;
	max-width: 1080px;
	margin: auto;
	padding: 20px;
	box-sizing: border-box;
	position: relative;
`

const BottomContainer = styled.div`
	position: absolute;
	bottom: 0%;
	padding-bottom: 12px;
	width: calc(100% - 40px);
`

const HeadContainer = styled.div`
	width: 100%;
	border-bottom: 1px solid #bbbbbb;
`

const CategoryContainer = styled.span`
	padding: 8px;
	background-color: black;
	margin-right: 4px;
	a {
		color: white;
		text-decoration: none;
	}
	@media (max-width: 1079px) {
		padding: 6px;
		font-size: 14px;
	}
`

const TitleContainer = styled.div`
	font-weight: 600;
	font-size: 32px;
	@media (max-width: 1079px) {
		font-size: 18px;
	}
`

const SummaryContainer = styled.span`
	font-size: 12px;
	color: white;
	margin-right: 10px;
`

const LittleContainer = styled.span`
	font-size: 14px;
	color: #bbbbbb;
`

const PostBanner = styled.div`
	width: 100%;
	height: 300px;
	color: white;
	position: relative;
	overflow: hidden;
`

const BlackContainer = styled.div`
	width: 100%;
	height: 100%;
	background-color: black;
	position: absolute;
	z-index: -1;
	opacity: 0.5;
`

const BackgroundContainer = styled.div`
	width: 100%;
	height: 100%;
	background-image: url('${props => props.src}');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	animation-name: zoomin;
    animation-duration: 5s;
	animation-fill-mode: both;
	z-index: -2;
	position: absolute;

	@-webkit-keyframes zoomin {
		from {transform: scale(1);}
		to {transform: scale(1.3);}
	}
	@keyframes zoomin {
		from {transform: scale(1);}
		to {transform: scale(1.3);}
	}
`

const Air = styled.div`
	width: 100%;
	height: 100px;
`

export default function Template({
  data, pageContext // this prop will be injected by the GraphQL query below.
}) {
	const { markdownRemark } = data // data.markdownRemark holds your post data
	const { frontmatter, html, id } = markdownRemark
	
	const description = html.replace(/<[^>]*>?/gm, '').substr(0,150)
	const tags = frontmatter.tags.map(x => <CategoryContainer key={x}><Link to={`/${x.toLowerCase()}`}>{x}</Link></CategoryContainer>)
	
	
	const disqusConfig = {
	  shortname: 'justkode',
	  config: { identifier: frontmatter.title, id },
	}
	
	return (
		<MainContainer>
			<Header />
			<SEO title={frontmatter.title} description={description} img={frontmatter.img} keyword={frontmatter.keyword}/>
			<PostBanner>
				<BlackContainer />
				<BackgroundContainer src={frontmatter.img || "/banner.jpg"} />
				<SubContainer style={{"height": "100%"}}>
					<BottomContainer>
						<div style={{"marginBottom": "8px"}}>{tags}</div>
						<TitleContainer>{frontmatter.title}</TitleContainer>
						<SummaryContainer>{frontmatter.summary}</SummaryContainer>
						<LittleContainer><FaRegCalendar/> {frontmatter.date}</LittleContainer>
					</BottomContainer>
				</SubContainer>
			</PostBanner>
			<SubContainer>
				<div className="markdown-body" dangerouslySetInnerHTML={{ __html: html }} />
				<Air />
				<DiscussionEmbed {...disqusConfig}/>
			</SubContainer>
			<Footer />
		</MainContainer>
	)
}

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
	  id
      frontmatter {
        date(formatString: "YYYY-MM-DD")
        path
        title
        tags
		keyword
		summary
        img
      }
    }
  }
`